/*game-over*/
.game-over {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    margin: auto;
    background: black;
    text-align: center;
    opacity: 0;
    display: none;
    transition: 2s;
}
.game-over img {
    max-height: 100%;
}
